body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

.centeredContent {
  margin: 0 auto;
  max-width: 1024px; }
  .centeredContent .bodyContent h1, .centeredContent .bodyContent h2, .centeredContent .bodyContent h3 {
    text-align: center; }
  .centeredContent .bodyContent h1 span {
    border-bottom: 1px solid #000000; }

.teamColorRadio {
  margin: 0.8em 0 0 0.8em; }
  .teamColorRadio:first-of-type {
    margin: 0; }

.playersList {
  margin: 1.5em 0;
  width: 70%; }
  .playersList tr th, .playersList tr td {
    padding: 0.8em; }

.rulesPartition {
  border-top: 1px dashed #000000; }

.rulesAccordion {
  padding: 2em 0; }

.accordion-list {
  list-style: none;
  margin: 0;
  padding: 0;
  background-color: #fff;
  border-radius: 0.4rem;
  overflow: hidden;
  box-shadow: 0 0 0.8rem 0.1rem rgba(15, 72, 179, 0.06), 0 20px 30px -10px rgba(15, 72, 179, 0.2); }
  .accordion-list__item + .accordion-list__item {
    border-top: 1px solid rgba(0, 0, 0, 0.1); }

.accordion-item--opened .accordion-item__icon {
  transform: rotate(180deg); }

.accordion-item--opened .accordion-item__inner {
  max-height: 100rem;
  transition-timing-function: cubic-bezier(0.895, 0.03, 0.685, 0.22);
  transition-duration: 0.5s;
  transition: max-height; }

.accordion-item--opened .accordion-item__content {
  opacity: 1;
  transform: translateY(0);
  transition-delay: 0.2s;
  transition-timing-function: ease-in-out;
  transition-duration: 0.2s;
  transition: opacity, transform; }

.accordion-item__line {
  display: block;
  padding: 0.8rem 1.2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #fff;
  z-index: 2;
  position: relative; }

.accordion-item__title {
  font-size: 1.6rem;
  margin: 0;
  font-weight: 700;
  color: #000000; }

.accordion-item__icon {
  width: 1.2rem;
  height: 1.2rem;
  transition: transform 0.3s ease-in-out;
  background-size: contain;
  background-repeat: no-repeat;
  background-image: url(data:image/png;base64,);
  opacity: 0.6; }

.accordion-item__inner {
  max-height: 0;
  overflow: hidden;
  text-transform: cubic-bezier(0.95, 0.05, 0.795, 0.035);
  transition-duration: 0.5s;
  transition: max-height;
  z-index: 1;
  position: relative; }

.accordion-item__content {
  opacity: 0;
  transform: translateY(-1rem);
  transition-timing-function: linear, ease;
  transition-duration: 0.1s;
  transition: opacity, transform;
  transition-delay: 0.5s;
  padding: 0 1.2rem 1.2rem; }

.accordion-item__paragraph {
  margin: 0;
  font-size: 1rem;
  color: #333;
  font-weight: 300;
  line-height: 1.3;
  white-space: pre-wrap; }

.footer {
  text-align: left;
  margin: 0 auto;
  padding-top: 16px;
  border-top: 2px solid #555f61; }
  .footer .info-text {
    float: left;
    padding-top: 16px; }
  .footer .footer-social {
    list-style-type: none;
    padding: 0; }
    .footer .footer-social .social-items {
      display: inline;
      padding-right: 8px; }
      .footer .footer-social .social-items .footer-href, .footer .footer-social .social-items .footer-href:visited {
        text-decoration: none;
        color: #06c; }
      .footer .footer-social .social-items .footer-href:focus, .footer .footer-social .social-items .footer-href:hover {
        color: #06c;
        text-decoration: underline; }

